import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatDialogModule } from '@angular/material';
import { MatIconModule } from '@angular/material/icon';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { PostItComponent } from './post-it/post-it.component';
import { DriehoekComponent } from './driehoek/driehoek.component';
import { Driehoek1Component } from './driehoek1/driehoek1.component';
import { Driehoek2Component } from './driehoek2/driehoek2.component';
import { Driehoek3Component } from './driehoek3/driehoek3.component';
import { Driehoek4Component } from './driehoek4/driehoek4.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { RouterModule, Routes } from '@angular/router';
import { HomeComponent } from './home/home.component';

const appRoutes: Routes = [
  { path: 'driehoek1', component: Driehoek1Component },
  { path: 'driehoek2', component: Driehoek2Component },
  { path: 'driehoek3', component: Driehoek3Component },
  { path: 'driehoek4', component: Driehoek4Component },
  { path: '', component: HomeComponent  },
  { path: '**', component: PageNotFoundComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    PostItComponent,
    DriehoekComponent,
    Driehoek1Component,
    Driehoek2Component,
    Driehoek3Component,
    Driehoek4Component,
    PageNotFoundComponent,
    HomeComponent
  ],
  imports: [
    RouterModule.forRoot(
      appRoutes
    ),
    BrowserModule,
    BrowserAnimationsModule,
    MatDialogModule,
    MatIconModule,
    MatProgressSpinnerModule
  ],
  providers: [],
  bootstrap: [AppComponent],
  entryComponents: [
    PostItComponent
  ]
})
export class AppModule { }
